<template>
  <div id="videoBackground">
    <div class="videoContainerBox">
      <video autoplay muted loop id="doggyVideo" poster="../assets/video/Puppy1280.jpg">
        <source src="../assets/video/Puppy640.mp4" type="video/mp4" media="all and (max-width: 768px)">
        <source src="../assets/video/Puppy1280.mp4" type="video/mp4" media="all and (min-width: 768px)">
        <!--source src="../assets/video/Puppy640.webm" type="video/webm"-->
      </video>
      <div class="content">
        <p class="videoOverlay">Дрессировка собак в Киеве и Киевской области</p>
        <!-- Use a button to pause/play the video with JavaScript -->
        <!--button id="myBtn" onclick="myFunction()">1</button-->
        <button class="playPauseButton paused" v-on:click="playPauseVideo"></button>
      </div>
    </div>
  </div>
</template>

<script>
// import store from '../store'

export default {
  name: 'videoBackground',
  props: {
    msg: String
  },
  mounted: function () {
    console.log('Mounted.')
    // this.showSlides(1)
  },
  methods: {
    playPauseVideo: function (event) {
      // console.log(`Play/pause video.`)
      // console.log(`${event.currentTarget}`)
      let video = document.getElementById('doggyVideo')
      if (video.paused) {
        video.play()
        event.currentTarget.classList.toggle('paused')
      } else {
        video.pause()
        event.currentTarget.classList.toggle('paused')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
$color-white: hsl(0, 0%, 100%);
$color-red: hsl(0, 100%, 41%);
$color-black: hsl(0, 0%, 0%);
$color-lightGray: hsl(0, 0%, 73%);
$color-grayShadow: hsla(0, 0%, 80%, 0.75);
$color-darkGray: hsl(0, 0%, 33%);

#videoBackground {
  width: 100%;
}

#doggyVideo {
    // position: fixed;
    // right: 0;
    // bottom: 0;
    // margin-top:.3em;
    width: 100%;
    // min-width: 100%;
    // min-height: 100%;
}

/* Add some content at the bottom of the video/page */
.content {
    position: relative;
    top: -5em;
    background: rgba(0, 0, 0, 0.4);
    color: #f1f1f1;
    width: 100%;
    // padding: 20px;
}

.videoOverlay {
  font-size: 1em;
  letter-spacing: .1em;
  padding: .4em;
  // z-index: 1;
}
/*
#myBtn {
    width: 200px;
    font-size: 18px;
    padding: 10px;
    border: none;
    background: #000;
    color: #fff;
    cursor: pointer;
}

#myBtn:hover {
    background: #ddd;
    color: black;
}
*/
.playPauseButton {
  position: absolute;
  left:1em;
  border: 0;
  background: transparent;
  box-sizing: border-box;
  width: 0em;
  top: -7em;
  height: 1.6em;
  border-color: transparent transparent transparent #ffffff;
  transition: 200ms all ease;
  cursor: pointer;

  // play state
  border-style: solid;
  border-width: .8em 0em .8em 1.4em;

  &.paused {
    border-style: double;
    height: 1.6em;
    border-width: 0em 0em 0em 1.6em;
  }

  &:hover {
    border-color: transparent transparent transparent #ff0000;
  }
}

@media only screen and (min-width: 768px) {
  .playPauseButton {
    top: -20em;
  }
  .content {
    top: -7em;
  }
  .videoOverlay {
    font-size: 2em;
    letter-spacing: .1em;
    padding: .4em;
    width: 100%;
  }
}

</style>
