<template>
    <div id="nav">
      <div class="containerBox">
        <div class="logo"><a href="/#home">MaxDog</a>
        </div>

        <div class="wideMenu">
          <a href="#">Услуги</a>
          <a href="/#grooming">Груминг</a>
          <router-link to="/reviews" >Отзывы</router-link>
          <router-link to="/gallery">Галерея</router-link>
          <router-link to="/philosophy">Наша философия</router-link>
          <a href="/#about">О нас</a>
          <a href="/#footer">Контакты</a>
        </div>

        <button v-on:click="showHideNavMenu" class="hamburger hamburger--collapse" type="button">
          <span class="hamburger-box">
            <span class="hamburger-inner"></span>
          </span>
        </button>
      <div id="mobileMenu" class="closed">
        <a href="#" v-on:click="showHideSubLinksMenu">Услуги
          <div class="dropDownContent hidden">
            <a class="subLink disabled">Послушание </a>
            <a class="subLink disabled">Защита </a>
            <a class="subLink disabled">Социализация собаки </a>
            <a class="subLink disabled">Цирковая дрессировка </a>
            <a class="subLink disabled">Подготовка к выставкам </a>
            <a class="subLink disabled">Помощь и консультации </a>
          </div></a>
        <a href="/#grooming" v-on:click="showHideNavMenu">Груминг</a>
        <router-link @click.native="showHideNavMenu()" to="/reviews" >Отзывы</router-link>
        <router-link @click.native="showHideNavMenu()" to="/gallery">Галерея</router-link>
        <router-link @click.native="showHideNavMenu()" to="/philosophy">Наша философия</router-link>
        <a href="/#about" v-on:click="showHideNavMenu">О нас</a>
        <a href="/#footer" v-on:click="showHideNavMenu">Контакты</a>
        <!--div class="slideThree">
          <input type="checkbox" v-on:click="comicSansMode" value="None" id="slideThree" name="check" checked/>
          <label for="slideThree"></label>
          <span>Comic Sans mode</span>
        </div-->
      </div>
      </div>
    </div>
</template>

<script>
import VueAnchorRouterLink from 'vue-anchor-router-link'
export default {
  name: 'Navigation',
  props: {
    msg: String
  },
  components: {
    VueAnchorRouterLink
  },
  methods: {
    showHideNavMenu (event) {
      const hamburgerIcon = document.querySelector('.hamburger')
      hamburgerIcon.classList.toggle('is-active')
      const mobileMenu = document.getElementById('mobileMenu')
      mobileMenu.classList.toggle('closed')
    },
    showHideSubLinksMenu (event) {
      console.log(`Hiding sublinks`)
      let elementToHide = document.querySelector('.dropDownContent')
      elementToHide.classList.toggle('hidden')
    },
    comicSansMode (event) {
      let headers = document.querySelectorAll('h1, h2, h3, h4, h5, h6')
      if (!event.currentTarget.checked) {
        for (let header of headers) {
          // console.log(`Value of current header is ${header}`)
          header.style.fontFamily = '"Spectral", serif'
        }
      } else {
        console.log(`Comic sans mode engage!`)
        for (let header of headers) {
          // console.log(`Value of current header is ${header}`)
          header.style.fontFamily = '"Comic Sans MS", cursive, sans-serif'
        }
      }
      // console.log(event.currentTarget.checked)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../assets/colors.scss";
@import "../assets/fonts.scss";
$hamburger-layer-width: 30px;
$hamburger-layer-color: $color-primary;

@import "../../node_modules/hamburgers/_sass/hamburgers/hamburgers.scss";

#nav {
  background-color: $color-white;
}
.containerBox {
  position: fixed;
  z-index: 1;
  height: 4em;
  width: 100%;
  color: $color-primaryPale2;
  display: flex;
  justify-content: space-between;
  background-color: $color-white;
  box-shadow: 0px 2px 5px 0px $color-grayShadow;
}
.logo {
  font-family: $logo-font;
  font-size: 2.5em;
  font-weight: 400;
  align-self: flex-start;
  padding: .2em 0em 0em .3em;
  a {
    color: $color-primary;
    text-decoration: none;
    transition-property: color;
    transition-duration: .3s;
    transition-timing-function: cubic-bezier(.46,.38,.89,.5);
  }
  a:hover {
    color: $color-link-hover;
  }
}

#mobileMenu{
  z-index: 1;
  top: 3.9em;
  overflow: hidden;
  max-height: 40em;
  position: absolute;
  width: 100%;
  text-align: center;
  background-color: $color-white;
  transition-duration: .5s;
  transition-timing-function: linear;
  box-shadow: 0px 3px 5px 0px $color-grayShadow;
  a {
    display: block;
    letter-spacing: .1em;
    text-decoration: none;
    color: $color-primaryPale2;
    font-size: 1.5em;
    font-weight: 700;
    line-height: 1.5;
    transition-property: color;
    transition-duration: .5s;
    transition-timing-function: cubic-bezier(.94,.27,.37,.97);
  }
  a:hover {
    color: $color-link-hover;
  }
}

#mobileMenu.closed {
  max-height: 0em;
  transition-property: max-height;
}

.dropDownContent {
  background-color: $color-lightGrayBackground;
  font-size: .5em;
  max-height: 18em;
  display: block;
  overflow: hidden;
  transition-duration: 1s;
  transition-timing-function: linear;
}

.dropDownContent.hidden {
  max-height: 0em;
  transition-property: max-height;
}
.slideThree {
    width: 80px;
    height: 26px;
    background: #333;
    margin: 20px auto;
    position: relative;
    border-radius: 50px;
    box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.5), 0px 1px 0px rgba(255, 255, 255, 0.2);
}

.slideThree:after {
    content: "OFF";
    color: #000;
    position: absolute;
    right: 10px;
    z-index: 0;
    font: 12px/26px Arial, sans-serif;
    font-weight: bold;
    text-shadow: 1px 1px 0px rgba(255, 255, 255, 0.15);
}

.slideThree:before {
    content: "ON";
    color: #27ae60;
    position: absolute;
    left: 10px;
    z-index: 0;
    font: 12px/26px Arial, sans-serif;
    font-weight: bold;
}

.slideThree label {
    display: block;
    width: 34px;
    height: 20px;
    cursor: pointer;
    position: absolute;
    top: 3px;
    left: 3px;
    z-index: 1;
    background: #fcfff4;
    // background: linear-gradient(top, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
    border-radius: 50px;
    transition: all 0.4s ease;
    box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.3);
}

.slideThree input[type=checkbox] {
    visibility: hidden;
}

.slideThree input[type=checkbox]:checked+label {
    left: 43px;
}

.slideThree span {
    position: absolute;
    width: 110px;
    top: 4px;
    right: 100px;
    color: #27ae60;
    // font: 26px Arial, sans-serif;
    // font-weight: bold;
    font-family: $header-font;
    letter-spacing: .2;
    font-size: .8em;
}
.wideMenu {
  display: none;
}

@media only screen and (min-width: 768px) {
  .wideMenu {
    // border: 1px solid lime;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    a {
      font-size: 1em;
      margin: 0em .3em 0em .3em;
      text-decoration: none;
      color: $color-primaryPale2;
      transition-property: color;
      transition-duration: .3s;
      transition-timing-function: cubic-bezier(.46,.38,.89,.5);
    }
    a:hover {
      color: $color-link-hover;
      text-decoration: underline;
    }
    /*
    a.router-link-active {
      color: $color-link-hover;
    }
    a.router-link-exact-active {
      color: $color-link-hover;
    }
    */
  }
  .hamburger {
    display: none;
  }
}

.grayDisabled {
  color: white;
}

@media only screen and (min-width: 1024px) {
  .wideMenu {
    a {
      font-size: 1.5em;
    }
  }
}
</style>
